body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__hour {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minute {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-minute;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center-point {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hour {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__minute {
  animation-duration: 10s;
}

@keyframes rotate-minute {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
