$hour: 3600s;
$minute: 60s;
$speedUp-Multiply: 6;

body {
  margin: 0;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.stopwatch {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  height: 80vmin;
  width: 80vmin;
  border-radius: 50%;
  border: dotted 1vmin #000;
}

.stopwatch__hour {
  position: absolute;
  bottom: 50%;
  height: 20vmin;
  width: 3vmin;

  background-color: #0700ff;

  animation: rotate-minute #{$hour} steps(60, end) infinite;
  transform-origin: bottom;
}

.stopwatch__minute {
  position: absolute;
  bottom: 50%;
  height: 38vmin;
  width: 1.5vmin;

  background-color: #2c8000;

  animation: rotate-minute #{$minute} infinite linear;
  transform-origin: bottom;
}

.stopwatch__center-point {
  border-radius: 50%;
  height: 5vmin;
  width: 5vmin;
  background-color: #f6a603;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hour {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__minute {
  animation-duration: 10s;
}

@keyframes rotate-minute {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
